.menu-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin: 20px;
  padding: 20px;
  border: 2px solid #ccc;
  border-radius: 10px;
  transition: all 0.3s ease;
  background-color: #f5f5f5;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.menu-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.menu-card img {
  width: 150px;
  height: 150px;
  margin-bottom: 10px;
}

.menu-card:hover {
  background-color: #e0e0e0;
}

.menu-card p {
  margin: 0;
  font-size: 18px;
}

.menu-card button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.menu-card button:hover {
  background-color: #0056b3;
}
